import classNames from 'classnames';
import ClearIcon from 'component/Icon/ClearIcon';
import IconClosedEyeOutlined14 from 'component/Icon/IconClosedEyeOutlined14';
import IconEye from 'component/Icon/IconEye';
import SearchIcon from 'component/Icon/SearchIcon';
import { useState } from 'react';
import styles from './index.less'
interface Props {
    placeholder?: string;
    value?: string;
    prefix?: any;
    allowClear?: boolean;
    onChange?: (e: any) => void;
    maxLength?: number;
    disabled?: boolean;
    type?: string;
    error?: string;
    password?: boolean;
    suffix?: string;
    className?: string;
}
const Input = (props: Props) => {

    const {
        placeholder, value,
        prefix, allowClear,
        onChange, maxLength,
        disabled, type, error,
        password, suffix, className,
    } = props;
    const [isFocus, setIsFocus] = useState(false);
    const [isHide, setIsHide] = useState(false)
    const handleFocus = () => {
        setIsFocus(true)
    }

    const handleBlur = () => {
        setIsFocus(false)
    }

    const handleMouseUp = (e: any) => {
        const target = e.currentTarget as HTMLSpanElement;
        const input = target.querySelector('input');
        if (input && isFocus === false) {
            input.focus();
        }
    }

    const getType = () => {
        if (password) {
            return isHide ? 'text' : 'password'
        }

        return 'text'
    }
    return (
        <div className={styles.parent}>
            <span className={classNames(styles.container, className, {
                [styles.active]: isFocus,
                [styles.disabled]: disabled,
                [styles.border]: type !== 'line',
                [styles.banBorder]: type === 'line'
            })}
                onMouseUp={handleMouseUp}
            >
                {
                    prefix || null
                }
                <input
                    spellCheck={false}
                    placeholder={placeholder}
                    type={getType()}
                    className={styles.input}
                    value={value}
                    onFocus={handleFocus}
                    onBlur={handleBlur}
                    onChange={onChange}
                    maxLength={maxLength}
                    disabled={disabled}
                />

                <span onClick={(e) => {
                    if (onChange) {
                        onChange({ target: { value: '' } });
                        e.stopPropagation();
                    }

                }}
                    className={value && allowClear ? styles.show : styles.onShow}
                >
                    <ClearIcon className={styles.clearIcon} />
                </span>

                {
                    password ? <span
                        className={styles.toggleIcon}
                        title={isHide ? '隐藏' : '展示'}
                        onClick={(e) => {
                            setIsHide(!isHide);
                            e.stopPropagation();
                        }}
                        onMouseUp={(e) => {
                            e.stopPropagation();
                        }}
                    >
                        {isHide ? <IconClosedEyeOutlined14 /> : <IconEye />}
                    </span> : suffix
                }

            </span>
            {
                error ? <p className={styles.error}>{error}</p> : null
            }
        </div>
    )
}

export default Input;