
import classNames from 'classnames';
import CheckBox from 'component/CheckBox';
import CursorInput from 'component/CursorInput';
import ClearIcon from 'component/Icon/ClearIcon';
import SearchIcon from 'component/Icon/SearchIcon';
import Input from 'component/Input';
import List from 'component/List';
import Popover from 'component/Popover';
import _ from 'lodash';
import { debounce } from 'lodash';
import { useEffect, useMemo, useState } from 'react';
import styles from './index.less'
const onSearch = (v: string) => { }
const Select = (props: any) => {

    const {
        isSearch, isMulti, list, value,
        onChange,
    } = props;

    const [searchText, setSearchText] = useState('');
    const [visible, setVisible] = useState(false);

    const dataSource = useMemo(() => {
        if (searchText !== '') {
            return list.filter((item: any) => item.content.includes(searchText))
        }
        return list;
    }, [list, searchText]);

    const renderItem = (index: number) => {
        const item = dataSource[index];
        const isActive = value.includes(item.key)
        if (isMulti) {
            return (
                <div
                    className={classNames(styles.row, {
                        [styles.active]: isActive
                    })}
                >
                    <CheckBox
                        options={[item]}
                        checkedKeys={value}
                        onChange={() => handleChange(item)}
                        style={{width: '100%', height: '100%'}}
                    ></CheckBox>
                </div>
            )
        }
        return <div
            className={classNames(styles.row, {
                [styles.active]: isActive
            })}
            onClick={() => handleChange(item)}
        >
            {item.content}
        </div>
    }



    const renderSearch = () => {
        return <div className={styles.inputContainer}>
            <Input
                allowClear
                value={searchText}
                prefix={<SearchIcon />}
                onChange={(e) => setSearchText(e.target.value)}
                type="line"
                placeholder="搜索"
                className={styles.input}
            ></Input>

        </div>
    }



    const handleChange = (item: any) => {
        if (!isMulti) {
            onChange([item.key]);
            setVisible(false)
            return;
        }

        const tempList = value.slice(0);
        const filterArr = tempList.filter((sub: any) => sub !== item.key);
        if (filterArr.length === tempList.length || !tempList.length) {
            filterArr.push(item.key)
        }
        onChange(filterArr)

    }
    const setSelectedOptions = (arr: any) => {
        const ids = arr.map((item: any) => item.key);
        onChange(ids)
    }

    const handleVisible = () => {
        setVisible(true)
    }


    return (

        <Popover
            content={<div >
                {isSearch ? renderSearch() : ''}
                <div className={styles.container}>
                    <List
                        list={dataSource}
                        render={renderItem}

                    >
                    </List>
                </div>
            </div>}
            placeholder="BL"
            visible={visible}
            trigged="popover"
            isArrow={false}
            onVisibleChange={() => { setVisible(false) }}
            overlayClass={styles.overlayClass}
        >
            <div className={styles.cursorInput} onClick={handleVisible}>
                <CursorInput
                    selectedOptions={list.filter((item: any) => value.includes(item.key))}
                    setSelectedOptions={setSelectedOptions}
                    placeHolder="请点击选择"
                ></CursorInput>
                {
                    <span className={!value.length ? styles.noShow : styles.clearIcon} onClick={() => onChange([])}>
                        <ClearIcon />
                    </span>
                }

            </div>
        </Popover>



    )
}

export default Select;
