import Select from "component/Select";
import _ from "lodash";
import { useState } from "react";
import styles from './index.less'
const list = [{ key: 1, content: '选项1' },
{ key: 2, content: '选项2' },
{ key: 4, content: '选项4' },
{ key: 5, content: '选项5' },
{ key: 6, content: '选项6' },
{ key: 7, content: '选项7' },
{ key: 8, content: '选项8' },
{ key: 9, content: '选项9' },
{ key: 10, content: '选项10' }]
const SelectMultiView = () => {
    const [value, setValue] = useState([]) as any;

    const handleChange = (t: any) => {
        setValue(t)
    }

    return (
        <div>
            <div className={styles.text}>具有输入框键盘删除功能</div>

            <Select
                value={value}
                list={list}
                onChange={handleChange}
                isSearch
                isMulti={true}
            ></Select>
        </div>
    )
}


export default SelectMultiView;