
import { useMemo, useState } from "react";
import styles from './index.less'
// 思路 传入一个数组，数组的内容的长度就是 光标的长度 通过光标的位置找到 要删除数组的index

// 删除的时候， 组件更新，光标不在设置位置，需要
enum KeyBoardCode {
    DELETE = 'Backspace',
    LEFT = 'ArrowLeft',
    RIGHT = 'ArrowRight'
}

const CursorInput = (props: any) => {

    const {selectedOptions, setSelectedOptions, placeHolder="请选择"} = props;

    const getValue = useMemo(() => {
        let str = '';
        selectedOptions.forEach((item: { content: any; }) => {
            str += `${item.content};`
        })
        return str;
    }, [selectedOptions])


    const handleKeyDown = (e: any) => {

        if (e.key === KeyBoardCode.DELETE) {
            const target = e.currentTarget as HTMLInputElement;
            const start = target.selectionStart || 0;  // 光标开始的位置

            let currentIndex = 0;
            let lastIndex = 0;
            let deleIndex = -1;
            for (let i = 0; i < selectedOptions.length; i++) {
                let index = selectedOptions[i].content.length + 1;
                currentIndex += index;
                if (start > lastIndex && start <= currentIndex) {
                    deleIndex = i;
                    break;
                }
                lastIndex += index;
            }
            if (deleIndex > -1) {
                const filter = selectedOptions.filter((item: any, index: number) => deleIndex !== index);
                setSelectedOptions(filter)
                // 需要等渲染网 selectedOptions // 光标回到位置
                setTimeout(() => {
                    target.setSelectionRange(lastIndex, lastIndex)
                }, 0)

            }
        }

        if (e.key === KeyBoardCode.LEFT || e.key === KeyBoardCode.RIGHT) {
            const target = e.currentTarget as HTMLInputElement;
            const start = target.selectionStart || 0;
            let currentIndex = 0;
            let lastIndex = 0;
            let toIndex = 0;

            for (let i = 0; i < selectedOptions.length; i++) {
                let index = selectedOptions[i].content.length + 1;
                currentIndex += index;

                // 光标在0的位置  光标要到尾部 left
                if (start === 0 && e.key === KeyBoardCode.LEFT) {
                    toIndex = selectedOptions.reduce((re: any, cur: { content: string | any[]; }) => re + cur.content.length + 1, 0);
                    break;
                }
                // 0 right 
                if (start === 0 && e.key === KeyBoardCode.RIGHT) {
                    toIndex = currentIndex;
                    console.log(toIndex, 'ppp');

                    break;
                }
                // 光标字在中间
                if (start >= lastIndex && start < currentIndex) {
                    if (e.key === KeyBoardCode.LEFT) {
                        toIndex = lastIndex;
                    } else {
                        toIndex = currentIndex;
                    }
                    break;
                }

                //在；后面
                if (start === currentIndex) {
                    if (e.key === KeyBoardCode.LEFT) {
                        toIndex = lastIndex;
                    } else {
                        const nextOption = selectedOptions[i + 1];
                        if (nextOption) {
                            toIndex = currentIndex + nextOption.content.length + 1;
                        }
                    }
                    break;
                }
                lastIndex += index
            }


            const where = toIndex

            target.setSelectionRange(where, where);
            target.blur();
            target.focus();


        }
        //  阻止默认事件 因为它都会进一位
        e.preventDefault()
    }
    // console.log(selectedOptions);

    return (
        <div>
            <input
                type="text"
                value={getValue}
                onKeyDown={handleKeyDown}
                className={styles.input}
                onChange={() =>{}}
                placeholder={placeHolder}
            />
        </div>
    )

}

export default CursorInput;
