export default "import Select from \"component/Select\";\nimport _ from \"lodash\";\nimport { useState } from \"react\";\nimport styles from './index.less'\nconst list = [{ key: 1, content: '选项1' },\n{ key: 2, content: '选项2' },\n{ key: 4, content: '选项4' },\n{ key: 5, content: '选项5' },\n{ key: 6, content: '选项6' },\n{ key: 7, content: '选项7' },\n{ key: 8, content: '选项8' },\n{ key: 9, content: '选项9' },\n{ key: 10, content: '选项10' }]\nconst SelectMultiView = () => {\n    const [value, setValue] = useState([]) as any;\n\n    const handleChange = (t: any) => {\n        setValue(t)\n    }\n\n    return (\n        <div>\n            <div className={styles.text}>具有输入框键盘删除功能</div>\n\n            <Select\n                value={value}\n                list={list}\n                onChange={handleChange}\n                isSearch\n                isMulti={true}\n            ></Select>\n        </div>\n    )\n}\n\n\nexport default SelectMultiView;";